<template lang="pug">

  .contact-info
    .info-header
      button(@click='goBack').info-header__option
        svg.info-header__icon
          use(xlink:href='#arrow')
        span Назад 
      button(v-if='userContact.name' @click='editContact').info-header__option Править

    .info-user-data
      .info-user
        .info-user__pic {{getUserPic}}
        p.info-user__name {{userContact.name || "Неизвестный номер"}}

    .info-user-actions

      button(@click='writeMessage').info-user-actions__option
        .info-user-actions__icon-wrapper
          img(src='../../assets/img/icons/message.svg').info-user-actions__icon
        span Сообщение

      button(@click='makeCall').info-user-actions__option
        .info-user-actions__icon-wrapper
          img(src='../../assets/img/icons/call.svg').info-user-actions__icon
        span Позвонить

    .info-user-phone
      p.info-user-phone__title Телефон
      p.info-user-phone__number {{phone}}

    .info-user-choices
      button(v-if='userContact.name' @click='deleteContact').info-user-choices__option Удалить контакт
      .info-user-choices__option(v-else @click='createContact') Добавить в контакты
      
      button(v-if='userContact.name' @click='addToFavourite').info-user-choices__option.
        {{ userContact.favourite ? "Удалить из избранных" : "Добавить в избранные" }}
        
      button(@click='block').info-user-choices__option.
        {{ blocked ? "Разблокировать" : "Заблокировать" }}

</template>

<script>
  
  import {mapState} from 'vuex'

  export default {
    props: ['phone'],
    computed: {
      ...mapState({
        contacts: state => state.calls.contacts,
        blockedNumbers: state => state.calls.blocked
      }),
      userContact() {
        let find =  this.contacts.find(item => item.number == this.phone);
        return find ? find : {user: null, number: this.phone}
      },
      getUserPic() {
        return this.userContact.name ? this.userContact.name[0].toUpperCase() : "?"
      },
      blocked() {
        return this.blockedNumbers.includes(parseInt(this.phone))
      }
    },
    methods: {
      makeCall() {
        if (this.blocked) 
          return this.$alert.show('Номер в черном списке')
          
        this.$store.dispatch('MAKE_CALL', {interlocutor: this.phone, type: 'outward'})
      },
      createContact() {
        this.$router.push({name: 'CreateContact', params: {options: {type: 'create', data: {number: this.phone}}}})
      },
      editContact() {
        this.$router.push({name: 'CreateContact', params: { options: {type: 'change', data: this.userContact}}})
      },
      writeMessage() {
        this.$router.push({name: 'MessageUser', params: {number: this.phone}})
      },
      goBack() {
        this.$router.go(-1)
      },
      deleteContact() {
        this.$router.go(-1);
        this.$store.commit('DELETE_CONTACT', this.phone);
        this.$alert.show('Контакт удален');
      },
      addToFavourite() {

        this.$store.commit('CHANGE_USER_FAVOURITE', 
          {number: this.phone, action: !this.userContact.favourite});

        this.userContact.favourite 
          ? this.$alert.show('Контакт добавлен в избарнные')
          : this.$alert.show('Контакт удален из избранных')

      },
      block() {
        this.$store.commit('BLOCK_NUMBER', parseInt(this.phone));

        this.blocked 
          ? this.$alert.show('Номер заблокирован')
          : this.$alert.show('Номер разблокирован')
      }
    }
  }

</script>

<style lang="sass"> @import '../../assets/sass/phone/apps/calls/info' </style>